:host {
  display: block;
  width: 100%;
  max-width: 500px;
  margin: auto;
  padding: 0 1rem;
  --p-iconfield-icon-color: var(--color-primary-color, #378bc0);

  @media (min-width: 532px) {
    padding: 0;
  }
}

.accounts-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  overflow-y: auto;
}

.account-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  border: 1px solid var(--surface-border);
  border-radius: var(--border-radius);
  gap: 0.75rem;
}

.account-avatar {
  background-color: var(--color-primary-color, #378bc0);
  color: white;
}

.account-select-icon {
  color: var(--color-primary-color, #378bc0);
}
